import { useState } from "react";
import EnterData from "./Top/EnterData";

export default function Top({ sendValue }) {
  const [addExpense, setAddExpense] = useState(false);

  function handleSetAddExpense() {
    setAddExpense(true);
  }

  function undoAddExpense(value) {
    setAddExpense(value);
  }

  function receiveValues(receivedValue) {
    sendValue(receivedValue);
  }

  return (
    <div>
      {addExpense ? (
        <EnterData onclick={receiveValues} undoaddexpense={undoAddExpense} />
      ) : (
        <div className="new-expense">
          <button onClick={handleSetAddExpense}>Add new Expense</button>
        </div>
      )}
    </div>
  );
}
