import ExpenseItem from "./Body/ExpenseItem";
import FilterExpense from "./Body/FilterExpense";
import Chart from "./Chart";
import { useState } from "react";

export default function Body({ sendValue }) {
  const [getYear, setGetYear] = useState("2021");
  const expenses = sendValue.filter((expense) =>
    expense.date.includes(getYear)
  );

  function handleYear(year) {
    setGetYear(year);
  }

  const filteredExpenses =
    getYear === "ALL"
      ? sendValue
      : sendValue.filter((expense) => expense.date.includes(getYear));

  return (
    <div className="expenses">
      <FilterExpense
        sendYear={sendValue}
        selectedYear={getYear}
        getyear={handleYear}
      />
      {getYear === "ALL" ? null : <Chart expenses={expenses} />}
      {filteredExpenses.length ? (
        filteredExpenses.map((expenseItem) => {
          return (
            <ExpenseItem
              key={expenseItem.id}
              title={expenseItem.title}
              amount={expenseItem.amount}
              date={expenseItem.date}
            />
          );
        })
      ) : (
        <h2 className="expenses-list__fallback">No Expenses</h2>
      )}
    </div>
  );
}
