export default function FilterExpense({ getyear, selectedYear, sendYear }) {
  function addYear() {
    const items = sendYear.map((preValue) =>
      new Date(preValue.date).getFullYear()
    );

    return [...new Set(items)].map((year) => {
      return (
        <option key={year} value={year}>
          {year}
        </option>
      );
    });
  }

  function updateSetGetYear(event) {
    getyear(event.target.value);
  }

  return (
    <div className="expenses-filter">
      <div className="expenses-filter__control">
        <label>Filter by year</label>
        <select value={selectedYear} onChange={updateSetGetYear}>
          <option value="ALL">ALL</option>
          {addYear()}
        </select>
      </div>
    </div>
  );
}
