import Top from "./Components/Top";
import Body from "./Components/Body";
import Footer from "./Components/Footer";
import * as uuid from "uuid";
import { useState } from "react";

export default function App() {
  const [values, setValues] = useState([]);
  // const [filterValues, setFilterValues] = useState(values);

  function receiveValues(receivedValue) {
    if (receivedValue.title && receivedValue.amount && receivedValue.date) {
      setValues((preValue) => {
        return [
          {
            id: uuid.v4(),
            title: receivedValue.title,
            amount: receivedValue.amount,
            date: receivedValue.date,
          },
          ...preValue,
        ];
      });
      receivedValue.title = "";
      receivedValue.amount = "";
      receivedValue.date = "";
    } else {
      alert("Fill all required field");
    }
  }

  return (
    <div>
      <Top sendValue={receiveValues} />
      <Body sendValue={values} />
      <Footer />
    </div>
  );
}
