import { useState } from "react";

export default function ExpenseData({ onclick, undoaddexpense }) {
  const [values, setValues] = useState({ title: "", amount: "", date: "" });

  function handleSetValue(event) {
    const { name, value } = event.target;
    setValues((preValue) => {
      return { ...preValue, [name]: value };
    });
  }

  return (
    <div className="new-expense">
      <form autoComplete="off" onSubmit={(e) => e.preventDefault()}>
        <div className="new-expense__controls">
          <div className="new-expense__control">
            <label>Title</label>
            <input
              onChange={handleSetValue}
              value={values.title}
              type="text"
              name="title"
            />
          </div>
          <div className="new-expense__control">
            <label>Amount</label>
            <input
              min="0.01"
              step="0.01"
              onChange={handleSetValue}
              value={values.amount}
              type="number"
              name="amount"
            />
          </div>
          <div className="new-expense__control">
            <label>Date</label>
            <input
              onChange={handleSetValue}
              value={values.date}
              type="date"
              name="date"
            />
          </div>
        </div>
        <div>
          <button
            type="button"
            onClick={() => {
              undoaddexpense(false);
            }}
          >
            Cancel
          </button>
          <button
            className="new-expense__actions"
            type="submit"
            onClick={() => {
              onclick(values);
            }}
          >
            Add Expense
          </button>
        </div>
      </form>
    </div>
  );
}
